.home-container{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    background: $color-1;
    min-height: 100vh;
    img{
        width: 100%;
    }
}
@media screen and (max-width: 900px) {
    .home-container{
        flex-direction: column;
    }
}